import { Navigate } from 'react-router-dom';
import { RoutesPaths } from '../../utills';
import { useAppSelector } from '../App/hooks-redux';
import Main from '../Main/Main';

function ProtectedMain() {
	const isUserAuthorized = useAppSelector(state => state.userAuthenticationData.isAuthorized);

	if (isUserAuthorized) {
		return <Main/>;
	}

	return <Navigate to={RoutesPaths.logIn}/>;
}

export default ProtectedMain;
